.App {
  width: 100vw;
  height: 100vh;
  display: flex;
}

.Map {
  flex-grow: 1;
}

.StateList {
  overflow-y: auto;
  padding: 0;
  margin: 0;
  width: 200px;
  flex-shrink: 0;
}

.StateListItem {
  padding: 10px;
  cursor: pointer;
  border-bottom: 1px solid #ccc;
  border-left: 1px solid #ccc;
}
